import { Component, OnInit, Inject } from '@angular/core';
import { MatTableDataSource, MAT_DIALOG_DATA, MatDialog, MatDialogRef, MatPaginator } from '@angular/material';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DashboardService } from '../dashboard/dashboard.service';
import { Routes, Router } from '@angular/router';
import * as Chartist from 'chartist';
import { Title } from '@angular/platform-browser';
import { HttpClient, HttpEventType } from '@angular/common/http';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  tableData: [];
  total_likes:any;
  total_dislikes:any;
  total_viewes:any;
  total_shares:any;
  total_downloads:any;
  total_users:any;

  constructor(
    public service: DashboardService,
  ) { }
  ngOnInit() {
    let payload = {};
    this.getlikes(payload);
  }
  getlikes(payload) {
    this.service.getlikes(payload)
      .subscribe(response => {
        // console.log(response, "response get analytics");
        // this.tableData = response;
        this.total_likes=response[0].total_likes;
        this.total_dislikes=response[0].total_dislikes;
        this.total_viewes=response[0].total_views;
        this.total_downloads=response[0].total_downloads;
        this.total_shares=response[0].total_share;
        this.total_users=response[1].tota_users;


      });

  }
}